@import url(https://fonts.googleapis.com/css?family=PT+Mono);
html,
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

h1, h2, h3, p {
  margin: 0;
  line-height: 1.2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Timer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.Timer__time {
  font-size: calc(40vh - 30px);
  line-height: calc(40vh - 30px);
  cursor: pointer;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
      user-select: none;
  font-family: 'PT Mono', mono;
}

.Timer__stop {
  border: 0;
  background: black;
  color: white;
  padding: 0 10px;
  height: 30px;
}

@media screen and (max-width: 800px) {
  .Timer__time {
    font-size: calc(22vh);
    line-height: calc(22vh);
  }
}

.Exercise {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  box-sizing: border-box;
  padding: 10px;
  display: flex;
  flex-direction: column;
  -webkit-transition: background-color 500ms ease,
    opacity 500ms ease,
    color 500ms ease;
  transition: background-color 500ms ease,
    opacity 500ms ease,
    color 500ms ease;
}

.Exercise__meta {
  flex: 0 0 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Exercise__phases {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Exercise__phase {
  font-size: 2rem;
  margin: 0 2rem;
  opacity: 0.1;
  -webkit-transition: opacity 500ms ease, color 500ms ease;
  transition: opacity 500ms ease, color 500ms ease;
}

.Exercise__phase--active {
  opacity: 1;
  color: white;
}

.Exercise__phase--complete {
  opacity: 1;
}

.Exercise__timer {
  flex: 0 0 40%;
}

.Exercise__progress {
  flex: 0 0 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
}

.Exercise__finish {
  border: 0;
  background: black;
  color: white;
  padding: 0 15px;
  height: 40px;
  font-size: 16px;
  line-height: 40px;
}

.Exercise__sets {
  margin: 0 0 10px;
}

.Exercise--over-resting {
  opacity: 0.5;
}

.Exercise--phase-endurance {
  background-color: #57a773;
}

.Exercise--phase-hypertrophy {
  background-color: #ee6352;
}

.Exercise--phase-power {
  background-color: #ee6352;
}

.Exercise--phase-strength {
  background-color: #08b2e3;
}

@media screen and (max-width: 800px) {

  .Exercise__meta {
    flex-basis: 40%;
  }

  .Exercise__timer {
    flex-basis: 40%;
  }

  .Exercise__progress {
    flex-basis: 20%;
  }

  .Exercise__phases {
    flex-wrap: wrap;
  }

  .Exercise__phase {
    font-size: 1.5rem;
  }

  .Exercise__progress {
    font-size: 1.5rem;
  }
}

.App {
}

.App__title {
  margin: 0;
}

